<template>
    <div>
        <NotificationService ref="notification" />
      <button @click="fetchData">📡 Appeler API</button>
      <p v-if="data">Données : {{ data }}</p>
    </div>
  </template>
  
  <script>
  import NotificationService from "./NotificationService.vue";
  
  export default {
    components: { NotificationService },
    data() {
      return {
        data: null,
        queue: []
      };
    },
    methods: {
      async fetchData() {
        if (!navigator.onLine) {
          alert("Pas de connexion. La requête sera envoyée plus tard.");
          this.queue.push("https://jsonplaceholder.typicode.com/todos/1");
          return;
        }
        try {
          const response = await fetch("https://jsonplaceholder.typicode.com/todos/1");
          const result = await response.json();
          this.data = result.title;
        } catch (error) {
          console.error("Erreur API", error);
        }
      }
    },
    mounted() {
      window.addEventListener("online", async () => {
        while (this.queue.length > 0) {
          await this.fetchData();
          this.queue.shift();
        }
        this.$refs.notification.sendNotification("✅ API Réponse !", "Les données ont été récupérées avec succès.");
      });
    }
  };
  </script>